import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { IFeatureRequest, IFeatureRequestCreateForm } from '@settings/models';

const URL = 'feature-requests';

@Injectable({
  providedIn: 'root'
})
export class FeatureRequestService {
  constructor(private apiService: ApiService,
  ) {}

  create(body: IFeatureRequestCreateForm) {
    const url = 'feature-requests/';
    return this.apiService.post<IFeatureRequest>(url, body);
  }
}
