import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ExtraSettingsComponent } from './extra-settings.component';

import {
  SettingsFaqComponent, SettingsFeatureRequestComponent,
} from './containers';

const routes: Routes = [
  {
    path: '',
    component: ExtraSettingsComponent,
    data: {
      title: 'EXTRA_SETTINGS.TITLE',
    },
    children: [
      {
        path: 'faq',
        component: SettingsFaqComponent,
      },
      {
        path: 'feature-request',
        component: SettingsFeatureRequestComponent,
      },
      {
        path: '**',
        redirectTo: 'faq',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ExtraSettingsRoutingModule {}
