import { Component, OnDestroy} from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { Subscription } from 'rxjs';
import { take } from 'rxjs/operators';

import { FaqService } from '@extraSettings/services/faq.service';
import { IFaq } from '@settings/models';

@Component({
  selector: 'j-settings-faq',
  templateUrl: './settings-faq.component.html',
  styleUrls: ['./settings-faq.component.scss']
})
export class SettingsFaqComponent implements OnDestroy {
  errorText = '';
  faqList: IFaq[];

  subscription = new Subscription();

  orderNum = 0;
  isLoading: boolean;

  constructor(
      private route: ActivatedRoute,
      private faqService: FaqService
    ) {
      const routeQuerySubscription = this.route.queryParams
        .pipe(take(1))
        .subscribe((queryParams) => {
          this.loadFaqList();
        });
  
      this.subscription.add(routeQuerySubscription);
    }
  
    ngOnDestroy(): void {
      this.subscription.unsubscribe();
    }

    async loadFaqList() {
      this.errorText = null;
      this.isLoading = true;
  
      try {
        const listResult = await this.faqService.getList();

        this.faqList = listResult.list;
        
        this.errorText = listResult.totalCount > 0 ? null : 'no data'
        this.orderNum = parseInt(this.faqList[this.faqList.length -1].orderNum) || 0;
      } catch (error) {
        this.errorText = error.status ? error.status : 'unknown error';
        this.faqList = [];
      } finally {
        this.isLoading = false;
      }
    }
}
