import { HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiService } from '@api/api.service';
import { IFaq, IFaqList } from '@settings/models';

const URL = 'faqs';

@Injectable({
  providedIn: 'root'
})
export class FaqService {
  constructor(private apiService: ApiService) {}

  async getFaq(id: string) {
    const url = `${URL}/${id}`;
    return this.apiService.get<IFaq>(url);
  }

  async getList(): Promise<IFaqList> {
    const url = URL;
    const headers = new HttpHeaders();

    let params = new HttpParams();

    const extras = {
      observe: 'response',
    };

    const response = await this.apiService.get<HttpResponse<IFaq[]>>(
      url,
      headers,
      params,
      extras
    );

    return {
      list: response.body,
      totalCount: response.body?.length || 0,
    };
  }
}
