import { Component, OnDestroy } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { BehaviorSubject, Subscription } from 'rxjs';

import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { NotifierService } from 'angular-notifier';

import { FeatureRequestService } from '@settings/services/feature-request.service';
import { FeatureRequestSuccessComponent } from '@settings/modals';
import { IFeatureRequestCreateForm } from '@settings/models';

const BODY_MAX_LENGTH = 1000;
enum NOTIFICATIONS {
  SAVE_REQUEST_ERROR = 'NOTIFICATIONS.SAVE_REQUEST_ERROR'
}
const TRANSLATE_PREFIX = 'SETTINGS.FEATURE_REQUEST';
@Component({
  selector: 'j-settings-feature-request',
  templateUrl: './settings-feature-request.component.html',
  styleUrls: ['./settings-feature-request.component.scss'],
})
export class SettingsFeatureRequestComponent implements OnDestroy {
  errorText = '';
  isLoading: boolean;
  bodyMaxLength = BODY_MAX_LENGTH;
  form: FormGroup;
  isSubmitting: boolean;
  bodyLengthMessage$ = new BehaviorSubject(`0/${this.bodyMaxLength}`);
  isSubmitted = false;
  subscription = new Subscription();

  constructor(private modalService: NgbModal,
              private featureRequestService: FeatureRequestService,
              private notifierService: NotifierService,
              private translateService: TranslateService) {
    this.form = new FormGroup({
      description: new FormControl('', [
        Validators.maxLength(this.bodyMaxLength),
        Validators.required,
        Validators.pattern('(?!^\\d+$)^.+$'),
        this.noWhitespaceValidator
      ]),
    });
    this.listenDescription();
  }

  public noWhitespaceValidator(control: FormControl) {
    const isWhitespace = (control.value || '').trim().length === 0;
    const isValid = !isWhitespace;
    return isValid ? null : { 'whitespace': true };
  }


  get f() {
    return this.form.controls;
  }


  async create() {
    if (this.isSubmitting) {
      return;
    }
    const canBeCreated = this.prepareCreate();

    if (!canBeCreated) {
      return;
    }
    this.isSubmitting = true;
    try {
      const body: IFeatureRequestCreateForm = { description: this.f.description.value };
      const result = await this.featureRequestService.create(body);
      this.isSubmitting = false;
      this.showSuccessModal();
    } catch (error) {
      this.showNotification(NOTIFICATIONS.SAVE_REQUEST_ERROR, 'error');
      this.isSubmitting = false;
    }
  }

  prepareCreate() {
    this.isSubmitted = true;
    return !this.form.invalid;
  }

  ngAfterViewInit() {
    const formSubscription = this.form.valueChanges.subscribe(
      (value) => {
        this.isSubmitted = false;
      },
    );
    this.subscription.add(formSubscription);
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  private listenDescription() {
    this.f.description.valueChanges.subscribe((description) => {
      this.bodyLengthMessage$.next(
        `${description.length}/${this.bodyMaxLength}`,
      );
    });
  }


  private async showSuccessModal() {
    const modal = this.modalService.open(FeatureRequestSuccessComponent, {
      backdropClass: 'backdrop_light',
      windowClass: 'modal_light modal_sm',
      backdrop: 'static',
      centered: true,
    });
    try {
      await modal.result;
      this.form.setValue({
        description: '',
      });
    } catch (error) {
    }
  }

  private showNotification(key: string, type: string = 'default') {
    const translateKey = this.mapTranslate(key);
    const message = this.translateService.instant(translateKey);

    this.notifierService.notify(type, message);
  }
  mapTranslate(key: string) {
    return `${TRANSLATE_PREFIX}.${key}`;
  }

  mapMessageErrors() {
    if (!this.f.description.errors) {
      return null;
    }
    if (this.f.description.errors.maxlength) {
      return 'ERRORS.MAX_LENGTH';
    }
  }
}
